import uuid from 'uuid';


/**
 * 
 * @enum {Symbol}
 * @since 3.2.0
 * @memberof module:leancloud-realtime
 */
const MessageStatus = {
  /**  */
  NONE: Symbol('none'),
  /**  */
  SENDING: Symbol('sending'),
  /**  */
  SENT: Symbol('sent'),
  /**  */
  DELIVERED: Symbol('delivered'),
  /**  */
  FAILED: Symbol('failed'),
};
Object.freeze(MessageStatus);

const rMessageStatus = {
  [MessageStatus.NONE]: true,
  [MessageStatus.SENDING]: true,
  [MessageStatus.SENT]: true,
  [MessageStatus.DELIVERED]: true,
  [MessageStatus.FAILED]: true,
};

export { MessageStatus };
export default class Message {
  /**
   * @implements AVMessage
   * @param  {Object|String} content 
   */
  constructor(content) {
    Object.assign(this, { content }, {
      /**
       * @type {String}
       * @memberof Message#
       */
      id: uuid.v4(),
      /**
       *  conversation id
       * @memberof Message#
       * @type {String?}
       */
      cid: null,
      /**
       * 
       * @memberof Message#
       * @type {Date}
       */
      timestamp: new Date(),
      /**
       * 
       * @memberof Message#
       * @type {String}
       */
      from: undefined,
      /**
       * 
       * @memberof Message#
       * @type {Boolean}
       */
      needReceipt: false,
      /**
       * 
       * @memberof Message#
       * @type {Boolean}
       */
      transient: false,
      /**
       * @var deliveredAt {?Date} 
       * @memberof Message#
       */
      // deliveredAt,
    });
    this._setStatus(MessageStatus.NONE);
  }

  /**
   * 
   * @param {Boolean} needReceipt
   * @return {Message} self
   */
  setNeedReceipt(needReceipt) {
    this.needReceipt = needReceipt;
    return this;
  }

  /**
   * 
   * @param {Boolean} transient
   * @return {Message} self
   */
  setTransient(transient) {
    this.transient = transient;
    return this;
  }

  /**
   *  JSON 
   * @protected
   * @return {Object}
   */
  toJSON() {
    return this.content;
  }

  /**
   *  {@link module:leancloud-realtime.MessageStatus} 
   * @type {Symbol}
   * @readonly
   * @since 3.2.0
   */
  get status() {
    return this._status;
  }

  _setStatus(status) {
    if (!rMessageStatus[status]) {
      throw new Error('Invalid message status');
    }
    this._status = status;
  }

  /**
   *  Message
   *  true
   * @protected
   * @returns {Boolean}
   * @implements AVMessage.validate
   */
  static validate() {
    return true;
  }

  /**
   * 
   * <pre>
   *  message message
   *  json  content  Message
   * @protected
   * @param  {Object}  json    json 
   * @param  {Message} message  message
   * @return {Message}
   * @implements AVMessage.parse
   */
  static parse(json, message) {
    return message || new this(json);
  }
}
