/** @module leancloud-realtime */
import Realtime from './realtime';
import Message, { MessageStatus } from './messages/message';
import TypedMessage from './messages/typed-message';
import TextMessage from './messages/text-message';
import {
  messageType,
  messageField,
  IE10Compatible,
} from './messages/helpers';

/**
 * 
 * @enum {Symbol}
 * @since 3.3.0
 * @memberof module:leancloud-realtime
 */
const OnlineStatus = {
  /**  */
  ONLINE: Symbol('online'),
  /**  */
  OFFLINE: Symbol('offline'),
};

export {
  /**
   * @see Realtime
   */
  Realtime,
  /**
   * @see Message
   */
  Message,
  /**
   * @see TypedMessage
   */
  TypedMessage,
  /**
   * @see TextMessage
   */
  TextMessage,
  /**
   * decorator
   * @function
   * @param {Number} type 
   * @example @messageType(1)
   * class CustomMessage extends TypedMessage {}
   *
   * //  decorator 
   * class CustomMessage extends TypedMessage {
   *   //...
   * }
   * messageType(1)(CustomMessage);
   */
  messageType,
  /**
   * decorator
   * @function
   * @param {String[]} fields 
   * @example @messageField(['foo'])
   * class CustomMessage extends TypedMessage {
   *   constructor(foo) {
   *     super();
   *     this.foo = foo;
   *   }
   * }
   *
   * //  decorator 
   * class CustomMessage extends TypedMessage {
   *   constructor(foo) {
   *     super();
   *     this.foo = foo;
   *   }
   *   //...
   * }
   * messageField(['foo'])(CustomMessage);
   */
  messageField,
  IE10Compatible,
  MessageStatus,
  OnlineStatus,
};

export {
  /**
   *  {@link https://leancloud.cn/docs/realtime_v2.html#}
   * @enum {Number}
   * @since 3.3.0
   */
  ErrorCode,
} from './error';
